<template>
  <div style="height: 100%">
    <a-layout v-if="!isServicePath" id="components-layout-demo-top" class="layout">
      <a-layout-header :style="{ position: 'fixed', zIndex: 1, width: '100%' }">
        <div class="logo">
        </div>
        <a-menu mode="horizontal"
                @click="routerTo"
                :defaultSelectedKeys="selectedKey"
                :style="{ lineHeight: '64px' }">
          <a-menu-item key="invoices">{{$t('Invoices')}}</a-menu-item>
          <a-menu-item key="products">{{$t('Products')}}</a-menu-item>
        </a-menu>
      </a-layout-header>
      <a-layout-content style="padding: 64px 0 0 0 ">
        <router-view/>
      </a-layout-content>
      <a-layout-footer :style="{zIndex: 1, width: '100%', bottom: 0}">
        Copyright © 2019 Whale Blue Network. All Rights Reserved.
      </a-layout-footer>
    </a-layout>
    <div v-if="isServicePath" class="user-dashboard">
      <router-view/>
    </div>
  </div>

</template>

<script>
  import router from '../router';

  export default {
    data() {
      return {
        token: '',
        selectedKey: [],
        openInvoicePage: false,
        isServicePath: location.href.indexOf('my/service') != -1
      };
    },
    mounted() {
      this.selectedKey.push(location.href.indexOf('products') > 0 ? 'products' : 'invoices');
    },
    methods: {
      routerTo({ item, key, keyPath }) {
        if (key === 'invoices' && !this.openInvoicePage && localStorage.getItem('inapp') === 'android') {
          this.openInvoicePage = true;
          this.firebaselogger.logEvent(this.firebaselogger.eventConfig.OPEN_INVOICE_PAGE, '');
        }
        router.push(key);
      },
    },
  };
</script>

<style lang="scss">

  .ant-layout-header {
    padding: 0;
  }
  .user-dashboard {
    height: 100%;
  }
</style>
